<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ info.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;  
             &nbsp; .    &nbsp;
            &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value">
                {{ info.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value">
                {{ info.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value"> {{ info.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ info.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value">    </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="product in info.basket.items"
              :key="product.product.id"
            >
              <h3>{{ product.product.title }}
                <span v-if="product.quantity > 1">  {{ product.quantity }}</span>
              </h3>
              <b>{{ (product.price * product.quantity) | numberFormat }} </b>
              <span>: {{ product.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>
              : <b>{{ totalAmount }}</b>   
              <b>{{ totalPrice | numberFormat }} </b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";

export default {
  created() {
    if (this.info && this.info.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch("loadOrder", this.$route.params.id);
  },
  computed: {
    info() {
      return this.$store.state.orderInfo;
    },
    totalPrice() {
      return this.info.basket.items.reduce(
        (acc, item) => acc + item.price * item.quantity,
        0,
      );
    },
    totalAmount() {
      return this.info.basket.items.reduce(
        (acc, item) => acc + item.quantity,
        0,
      );
    },
  },
  filters: {
    numberFormat,
  },
};
</script>

