<script>
import axios from "axios";
import { BASE_API_URL } from '@/constants';
import numberFormat from '@/helpers/numberFormat';
import AmountInput from "@/components/AmountInput.vue";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      currentColor: 0,
      productAmount: 1,
      productData: null,
      categoriesData: null,
      colorsData: null,
      productLoaded: false,
      productLoadError: false,
      productAdded: false,
      productSending: false,
    };
  },
  components: {
    AmountInput,
  },
  computed: {
    product() {
      return this.productData ? this.productData : {};
    },
    category() {
      return this.productData.category;
    },
    colorList() {
      return this.productData.colors;
    },
  },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      this.productAdded = false;
      this.productSending = true;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      }).then(() => {
        this.productAdded = true;
        this.productSending = false;
      });
    },
    loadProduct() {
      this.productLoaded = false;
      this.productLoadError = false;
      axios
        .get(
          `${BASE_API_URL}/products/${this.$route.params.id}`,
        )
        .then((response) => {
          this.productData = response.data;
        })
        .catch(() => {
          this.productLoadError = true;
        }).then(() => {
          this.productLoaded = true;
        });
    },
  },

  created() {
    this.loadProduct();
  },
  watch: {
    '$route.params.id': function () {
      this.loadProduct();
    },
  },
};
</script>
